import { build } from './build';
import { evaluatePostfixShunted } from './evaluatePostfixShunted';

// ShuntingYard
function compile(expression: string) {
  return evaluatePostfixShunted(build(expression))
}

export { compile }

// Example usage:
const expression = "";
console.log(compile(expression)); // Should output: 3.0001220703125